    };
  },
  setup() {
  }, // API
  created() {
  }, //  
  mounted() {
  }, //template  html    
  unmounted() {
  }, //unmount   
  methods: {}, //    
  watch: {
    firstName() {
      this.fullName = this.firstName + ' ' + this.lastName;
    },
    lastName() {
      this.fullName = this.firstName + ' ' + this.lastName;
    }
  }
}
</script>

