import { createApp } from 'vue'
import App from './App.vue'
import router from './router' //router  index.js import
import mixins from '../blob/master/src/mixins'
import store from "../blob/master/src/store";
import i18n from '/src/plugins/i18n'

const i18nStrings = {
    en: {
        hi: 'Hello!'
    },
    ko: {
        hi: '!'
    }
}

const app = createApp(App)
app.use(router)
app.use(store)
app.use(i18n, i18nStrings)
app.mixin(mixins)
app.mount('#app')

// createApp(App) >   App.vue app 
// use(router) > App.vue     
// mixin(mixins) >     
// mount('#app') > App.vue public  index.html   id="app" html element 

app.directive('focus', {
    mounted(el) {
        el.focus()
    }
})
// vue  <input type="text" v-focus />   