<template>
  <div>
    <h2>CalculatorAPI3</h2>
    <div>
      <input type="text" v-model="num1" />
      <span> + </span>
      <input type="text" v-model="num2" />
      <span> = </span>
      <span>{{result}}</span>
    </div>
  </div>
</template>

<script>
import {reactive, computed, toRefs} from 'vue';
function plusCalculator() {
  let state = reactive({
    num1: 0,
    num2: 0,
    result: computed(() => parseInt(state.num1) + parseInt(state.num2))
  });
  return toRefs(state);
}
export default {
  name: "Calculator", // 
  components: {}, //    import( )
  data() { //html js    
    return {
    };
  },
  setup() {
    let {num1, num2, result} = plusCalculator(); //() function
    return {
      num1, num2, result
    }
  }, // API
  created() {
  }, //  
  mounted() {
  }, //template  html    
  unmounted() {
  }, //unmount   
  methods: {
  } //    
}
</script>

