<template>
  <h1>Hello, {{title}}!</h1>
  <!-- {{xxx}} > data   -->
  <div style="height: 1000px;">
    <p v-pin="position">  (position:fixed;top:50px,left:100px;)</p>
  </div>
</template>

<script>
export default {
  name: "DataBinding", // 
  data() { //html js    
    return {
      title: 'World',
      position: {top: 600, left: 150}
    };
  },
  directives: {
    pin: {
      mounted(el, binding) {
        el.style.position = 'fixed';
        el.style.top = binding.value.top + 'px';
        el.style.left = binding.value.left + 'px';
      }
    }
  }
}
</script>

