<template>
  <h1>Full Name : {{fullName}}</h1>
  <button type="button" @click="changeName">changeName</button>
</template>

<script>
export default {
  name: "Computed", // 
  component: {}, //    import( )
  data() { //html js    
    return {
      firstName: 'Sooyeon',
      lastName: 'Kim',
    };
  },
  setup() {
  }, // API
  created() {
  }, //  
  mounted() {
  }, //template  html    
  unmounted() {
  }, //unmount   
  methods: {
    changeName(){
      this.firstName = 'Change';
    }
  }, //    
  computed: {
    fullName() {
      return this.firstName + ' ' + this.lastName;
    }
  }
}
</script>

